import React, { useState, useContext } from 'react';
import SearchContext from '../context/search';
import SearchIcon from '../images/searchIcon.png';
import '../styles/SearchBar.css';

export default function SearchBar() {
  const [filter, setFilter] = useState('');
  const [query, setQuery] = useState('');
  const { search } = useContext(SearchContext);

  return (
    <div className="searchBar">
      <div>
        <input
          className="input-text"
          type="text"
          data-testid="search-input"
          // placeholder="Encontre sua receita aqui..."
          value={ query }
          onChange={ (e) => setQuery(e.target.value) }
        />
        <button
          className="searchIcon"
          type="button"
          data-testid="exec-search-btn"
          onClick={ () => search(filter, query) }
        >
          <img
            src={ SearchIcon }
            data-testid="search-top-btn"
            alt="Search"
          />
        </button>
      </div>
      <div className="filters">
        <label htmlFor="radio-name">
          <input
            type="radio"
            data-testid="name-search-radio"
            name="radio"
            id="radio-name"
            checked={ filter === 'name' }
            onChange={ () => setFilter('name') }
          />
          Name
        </label>
        <label htmlFor="radio-ingredient">
          <input
            type="radio"
            data-testid="ingredient-search-radio"
            name="radio"
            id="radio-ingredient"
            checked={ filter === 'ingredient' }
            onChange={ () => setFilter('ingredient') }
          />
          Ingredient
        </label>
        <label htmlFor="radio-first-letter">

          <input
            type="radio"
            data-testid="first-letter-search-radio"
            name="radio"
            id="radio-first-letter"
            checked={ filter === 'first-letter' }
            onChange={ () => setFilter('first-letter') }
          />
          First letter
        </label>
      </div>
    </div>
  );
}
