import React, { useEffect, useMemo, useState } from 'react';
import { useParams, useLocation, useHistory } from 'react-router-dom';
import clipboardCopy from 'clipboard-copy';
import Carousel from 'react-bootstrap/Carousel';
import { fetchById, fetchByName } from '../services/searchAPI';
import FavoriteButton from '../components/FavoriteButton';
import '../styles/PagesRecipes.css';
import IconShare from '../images/shareIcon.svg';

const MAX_CARDS = 6;

export default function RecipeDetails() {
  const [itemRecommended, setItemRecommended] = useState([]);
  const [recipe, setRecipe] = useState({
    ingredients: [],
  });

  const { id } = useParams();
  const location = useLocation();
  const history = useHistory();

  const type = useMemo(() => ({
    route: location.pathname.includes('foods') ? '/foods' : '/drinks',
    path: location.pathname.includes('foods') ? 'meal' : 'cocktail',
    name: location.pathname.includes('foods') ? 'Meal' : 'Drink',
    recommendation: {
      path: location.pathname.includes('foods') ? 'cocktail' : 'meal',
      name: location.pathname.includes('foods') ? 'Drink' : 'Meal',
    },
  }), [location.pathname]);

  const isDone = useMemo(() => (
    localStorage.getItem('doneRecipes')
    && JSON.parse(
      localStorage.getItem('doneRecipes'),
    ).some(({ id: recipeId }) => (
      recipeId === id
    ))
  ), [location.pathname]);

  const inProgress = useMemo(() => (
    localStorage.getItem('inProgressRecipes')
    && Object.keys(
      JSON.parse(localStorage.getItem('inProgressRecipes'))[`${type.path}s`],
    ).some((key) => (
      key === id
    ))
  ), [location.pathname]);

  useEffect(() => {
    const fetchAPI = async () => {
      const item = await fetchById(id, type.path);
      const recommended = await fetchByName('', type.recommendation.path);

      setItemRecommended(recommended);
      setRecipe(item);
    };

    fetchAPI();
  }, []);

  const handleCopy = (e) => {
    e.target.innerText = 'Link copied!';

    clipboardCopy(window.location.href);

    setTimeout(() => {
      e.target.innerText = 'Share';
    }, Number('750'));
  };
  return (
    <div className="main">
      <img
        className="thumb"
        src={ recipe[`str${type.name}Thumb`] }
        alt={ recipe[`str${type.name}`] }
        data-testid="recipe-photo"
      />
      <div className="title">
        <h1 data-testid="recipe-title">
          {recipe[`str${type.name}`]}
          <h6
            className="subtitle"
            data-testid="recipe-category"
          >
            {'('}
            {recipe.strCategory}
            &nbsp;
            {recipe.strAlcoholic}
            {')'}
          </h6>
        </h1>
        <div className="title-buttons">
          <button
            className="pink-button"
            type="button"
            data-testid="share-btn"
            onClick={ (e) => handleCopy(e) }
          >
            <img src={ IconShare } alt="button share" />
          </button>
          <FavoriteButton recipe={ recipe } />
        </div>
      </div>

      <div className="ingredients">
        <h3>Ingredients</h3>
        <div className="list-ingrendients">
          {recipe.ingredients.map((ingredient, index) => (
            <p
              data-testid={ `${index}-ingredient-name-and-measure` }
              key={ index }
            >
              <strong>{ingredient.name}</strong>
              &nbsp;
              <span>{ingredient.qty}</span>
            </p>
          ))}
        </div>
      </div>
      <div className="ingredients">
        <h3>Instructions</h3>
        <p data-testid="instructions">
          {recipe.strInstructions}
        </p>
      </div>
      {recipe.strYoutube && (
        <div className="video">
          <iframe
            width="400"
            height="250"
            data-testid="video"
            src={ `https://www.youtube.com/embed/${recipe.strYoutube.split('?v=')[1]}` }
            title="YouTube video player"
            frameBorder="0"
            allow="accelerometer;
              clipboard-write; encrypted-media;
              gyroscope; picture-in-picture"
            allowFullScreen
          />
        </div>
      )}
      <div className="div-recommendation">
        <div className="recommendation">
          <h2>Drink Recommendation</h2>
          <Carousel>
            {itemRecommended.slice(0, MAX_CARDS).map((recom, index) => (
              <Carousel.Item
                className="teste"
                type="button"
                key={ index }
                onClick={ () => history.push(rote(recom)) }
                data-testid={ `${index}-recomendation-card` }
              >
                <img
                  className="d-block w-100"
                  data-testid={ `${index}-card-img` }
                  src={ recom[`str${type.recommendation.name}Thumb`] }
                  alt={ `${recom[`str${type.recommendation.name}`]}` }
                />
                <Carousel.Caption>
                  <h5 data-testid={ `${index}-recomendation-title` }>
                    {recom[`str${type.recommendation.name}`]}
                  </h5>
                </Carousel.Caption>
              </Carousel.Item>
            ))}
          </Carousel>
        </div>
      </div>
      <div className="button-start">
        {
          !isDone && (
            <button
              type="button"
              data-testid="start-recipe-btn"
              onClick={ () => history.push(`${type.route}/${id}/in-progress`) }
            >
              {inProgress ? 'Continue Recipe' : 'Start Recipe'}
            </button>
          )
        }
      </div>
    </div>
  );
}
