import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import drinkIcon from '../images/drinkIcon.svg';
import mealIcon from '../images/mealIcon.svg';
import '../styles/IconsFoods.css';

export default function IconsFoods() {
  const location = useLocation();

  return (
    <div
      className="main-iconsFoods"
      data-testid="footer"
    >
      <Link to="/drinks">
        <img
          className={ !location.pathname.includes('foods') ? 'clickOn' : 'clickOff' }
          src={ drinkIcon }
          data-testid="drinks-bottom-btn"
          alt="Drink"
        />
      </Link>
      <Link to="/foods">
        <img
          className={ !location.pathname.includes('foods') ? 'clickOff' : 'clickOn' }
          src={ mealIcon }
          data-testid="food-bottom-btn"
          alt="Meal"
        />
      </Link>
    </div>
  );
}
