import React, { useContext, useMemo } from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import SearchContext from '../context/search';
import Header from '../components/Header';
import IconsFoods from '../components/IconsFoods';
import '../styles/Recipes.css';

export default function Recipes() {
  const location = useLocation();
  const { items, categories, search } = useContext(SearchContext);
  const MAX_RESULTS = 12;

  const history = useHistory();

  const type = useMemo(() => (
    location.pathname.includes('foods') ? 'Meal' : 'Drink'
  ), [location.pathname]);

  const rote = (item) => {
    if (!location.pathname.includes('foods')) {
      return `/drinks/${item[`id${type}`]}`;
    }
    return `/foods/${item[`id${type}`]}`;
  };

  return (
    <>
      <Header />
      <div className="filtes-recepipes">
        <button
          type="button"
          data-testid="All-category-filter"
          onClick={ () => search('', '') }
        >
          All
        </button>
        {categories.map(({ strCategory: category }, index) => (
          <button
            type="button"
            key={ index }
            data-testid={ `${category}-category-filter` }
            onClick={ () => search('category', category) }
          >
            {category}
          </button>
        ))}
      </div>
      <IconsFoods />
      <div className="main-recepipes">
        <div className="recipes">
          {
            (
              Array.isArray(items)
            ) && items.slice(0, MAX_RESULTS).map((item, index) => (
              <button
                type="button"
                key={ index }
                data-testid={ `${index}-recipe-card` }
                onClick={ () => history.push(rote(item)) }
              >
                <img
                  data-testid={ `${index}-card-img` }
                  src={ item[`str${type}Thumb`] }
                  alt={ `${item[`str${type}`]}` }
                  style={ {
                    width: '100%',
                    maxWidth: '80vw',
                  } }
                />
                <p
                  data-testid={ `${index}-card-name` }
                >
                  {item[`str${type}`]}
                </p>
              </button>
            ))
          }
        </div>
      </div>

    </>
  );
}
